body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.calc-value {
  font-weight: bold;
  color: #195d19;
  font-size: 1.2em;
  margin-left: 5px;
}
p.errorMessage {
  color: #d8391d;
  margin-top: 5px;
  margin-bottom: 5px;
}
.eventsControls {
  padding-bottom: 30px;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

.eventsControls > span {
  border-bottom: 3px solid #ccc;
  transition: all .25s ease;
  cursor: pointer;
  display: inline-block;
}
.eventsControls > span:first-child {
  margin-left: 0px;
}
.eventsControls > span.active {
  border-bottom: 3px solid #5d5c19;
}

label {
  display: block;
  margin-bottom: 5px;
}

input {
  margin-left: 10px;
}
.formulasContainer {
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  margin-top: 40px;
}

.formulasContainer > span {
  display: inline-block;
  border-bottom: 3px solid #ccc;
  transition: all .25s ease;
  cursor: pointer;
}

.formulasContainer > span.active {
  border-bottom: 3px solid #e28935;
}
button.clear-button {
  margin-left: 15px;
} 
.page-header {
  text-align: center;
}
.flexbox {
  display: -webkit-flex;
  display: flex;
}

body {
  box-sizing: border-box;
}

input[type="submit"] {
  margin: auto;
  display: inline-block;
  background: #195d19;
	color: inherit;
  border: none;
  border-radius: 8px;
	padding: 8px 40px;
	font: inherit;
	cursor: pointer;
  outline: inherit;
  color: white;
  font-size: 0.95em;
  transition: background .25s ease;
  margin-top: 40px;
  margin-bottom: 20px;
}

input[type="submit"]:hover {
  background: #257525;
}

.dashboard-formula {
  box-shadow: 2px 2px 6px 0 rgba(0, 0, 0, 0.3);
  padding: 10px 40px;
  width: 80%;
  margin: auto;
  margin-top: 50px;
}

.dashboard-formula-header {
  text-align: center;
  margin-top: 40px;
}

.dashboard-formula form {
  padding-top: 10px;
}

.clear-button {
  margin: auto;
  display: inline-block;
  background: #d8391d;
	color: inherit;
  border: none;
  border-radius: 8px;
	padding: 8px 40px;
	font: inherit;
	cursor: pointer;
  outline: inherit;
  color: white;
  font-size: 0.95em;
  transition: background .25s ease;
  margin-top: 10px;
  margin-bottom: 20px;
}

.clear-button:hover {
  background: #f34627;
}

.formula-image {
  max-width: 25em;
}
